import React, { useEffect, useState } from "react";
import { Button, Card, Carousel, Col, Row } from "antd";
import * as Realm from "realm-web";

import {
  GlobalOutlined,
  YoutubeOutlined,
  InstagramOutlined,
  FacebookOutlined,
  LinkedinFilled,
  GithubOutlined,
} from "@ant-design/icons";

import { DeviceSizeView, Separator, VideoSlide } from "components/common";

import logo from "assets/avatar.svg";

const background = {
  background:
    'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/img/backgrounds/neon.jpg")',
  backgroundSize: "cover",
  backgroundPosition: "center",
};

const button = {
  fontSize: "1.8rem",
  border: "none",
  width: "10rem",
};

function calculateAge(birthday) {
  // birthday is a date
  var ageDifMs = Date.now() - birthday.getTime();
  var ageDate = new Date(ageDifMs); // miliseconds from epoch
  return Math.abs(ageDate.getUTCFullYear() - 1970);
}

const Home = () => {
  const [news, setNews] = useState([]);

  return (
    <>
      <div className="slide slide-first white-text" style={background}>
        <div className="container centered">
          <img
            src={logo}
            alt="mascotte"
            style={{ maxWidth: "400px", margin: "20px" }}
          />
          <h1>Hi everyone!</h1>
        </div>
      </div>

      <div className="slide dotted">
        <div className="container centered">
          <div>
            <h2>Who am I?</h2>

            <Separator height={2} />

            <h5 className="description">
              I'm a {calculateAge(new Date(1995, 12, 9))}-years-old{" "}
              <b>Computer Engineer</b> and <b>Deep Learning</b> researcher at
              the University of Catania.
              <Separator height={1.6} />
              I'm also a free time photographer and 3D graphics geek.
              <br />
              I love watching movies, reading books, eating pizza and drinking
              beer.
              <Separator height={1.6} />
              I'm also the proud co-founder of{" "}
              <a href="https://www.weirdframes.com">Weird Frames</a>.
            </h5>

            <Separator height={3} />

            <div>
              <Button
                href="https://www.linkedin.com/in/giuvecchio/"
                style={button}
              >
                <LinkedinFilled />
              </Button>

              <Button href="https://github.com/giuvecchio" style={button}>
                <GithubOutlined />
              </Button>
            </div>

            <Separator height={6} />

            <Row gutter={[32, 32]}>
              <Col sm={24} md={12}>
                <img
                  style={{ maxWidth: "100%" }}
                  src="/img/pictures/then-now.jpg"
                  alt="then-now"
                />
              </Col>
              <Col sm={24} md={12} style={{textAlign:"left"}}>
                <p>
                  My interest in computer science dates back to when I was a
                  child, fascinated by what that strange object called a
                  computer could accomplish.{" "}
                </p>
                <p>
                  Films and other media, such as <b>Blade Runner</b> and{" "}
                  <b>Ex_Machina</b>, did the rest, fueling the interest in what
                  could be gained from the digital world.{" "}
                </p>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
